@import url(https://use.typekit.net/sia7taz.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


  .App {
  text-align: center;
  background-color:orangered;
  width: 100vw;
  height: 100vh;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: arpona;
}

header footer{
  height: 10vh;
  width: 100vw;
  background-color: orange;
}

input{
  border-radius: 25px;
  width: 200px;
  height: 55px;
  border: none;
  box-shadow: lightslategray 0 9px;
  
}

button{
  border-radius: 30%;
  border: none;
  height: 60px;
  display: inline-block;
  top: 10%;
  padding: 15px 20px;
  box-shadow: lightslategray 0 9px;
  
}
button:hover{
  cursor: pointer;
  background-color: lightgrey;

}

button:active{
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
  box-shadow: 0 5px;
}

.frame{
  max-width: 500px;
  max-height: 500px;
  min-width: 500px;
  min-height: 500px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: black;
  border-radius: 10%;
  border:20px;
}

.gif{
  position:absolute;
  -webkit-align-self: center;
          align-self: center;
  justify-self: center;
}

form{
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  display:inline-block;
  /* background-color: lightskyblue; */
}

img{
  max-width: 500px;
  max-height: 500px;
  
}

